import React, {Component} from 'react';


export default class Grid extends Component{

    constructor(props){
        super(props);

        this.state = {
            grid: props.gridArray,
            countRows: 10,
            countColumn: 32,
        };
    }

    makeGrid(){
        let grid = [];

        for(let i = 0;i < this.state.grid[0].length;i++){
            grid[i] = [];
            for(let j = 0;j < this.state.grid.length;j++){
                grid.push(
                    <div className="cell" key={i*10 + j}>
                        {0}
                    </div>
                );
            }
        }


        return grid;
    }

    render(){
        let grid = this.makeGrid();
        return(
            <div className="Grid">
                {
                   grid
                }
            </div>
        );
    }
}